<!--交易记录-->
<template>
  <div class="TransactionRecord">
    <el-card>
      <div style="display: flex; width: 100%;height: 50px;padding-left: 5px; margin: 10px 0 20px;">
        <div style="width: 120px; line-height: 40px;">自定义查询：</div>
        <el-select v-model="selectable.value" placeholder="请选择查询方式">
          <el-option v-for="item in selectable.list" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <!-- 日期选择 -->
        <div style="width: 400px;position: relative;" v-show="selectable.value=='1'">
          <el-date-picker v-model="queryMsg.time" type="daterange" align="center" unlink-panels range-separator="至"
            start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions"
            value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
          <div style="position: absolute;top: 47px;left: 27px;font-size: 12px;color: #c0c4cc">
            提示：不选择时间，默认查询全部记录
          </div>
        </div>
        <!-- 核销码输入 -->
        <div style="display: flex;width: 400px;position: relative;" v-show="selectable.value=='2'">
          <div style="width: 100px; line-height: 40px;">核销码：</div>
          <el-input :maxlength="7" v-model="queryMsg.code" style="width: 275px;"></el-input>
          <div style="position: absolute;top: 47px;left: 100px;font-size: 12px;color: #c0c4cc">
            提示：不输入核销码，默认查询全部记录
          </div>
        </div>
        <!-- 手机号输入 -->
        <div style="display: flex;width: 400px;position: relative;" v-show="selectable.value=='3'">
          <div style="width: 100px; line-height: 40px;">手机号：</div>
          <el-input :maxlength="11" v-model="queryMsg.phone" style="width: 275px;"></el-input>
          <div style="position: absolute;top: 47px;left: 100px;font-size: 12px;color: #c0c4cc">
            提示：不输入手机号，默认查询全部记录
          </div>
        </div>
        <!-- 查询、导出按钮 -->
        <div style="height: 40px;margin-left:20px;">
          <el-button type="primary" @click="query_name">查询</el-button>
          <el-button type="primary" @click="platformAddRole"><i class="el-icon-edit">导出</i></el-button>
        </div>
      </div>
      <!--  页面表格 start   -->
      <el-table class="account-plat-fixed-right account-plat-fixed" border ref="tableData" :data="tableData"
        tooltip-effect="dark" @sort-change="sortColumn" :row-style="{'height':'10px'}"
        :cell-style="{'padding':'5px 0px'}"
        :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' ,'padding':'0px','height':'45px','line-height':'45px' }">
        <el-table-column fixed align="center" type="index" label="序号" width="50">
        </el-table-column>

        <el-table-column v-for="(item,index) in formData.tableData" :key="index" :prop="item.value" :label="item.key"
          align="center" show-overflow-tooltip width="150">
          <template slot-scope="scope">
            <div v-if="item.identification==1">
              <el-tag v-if="scope.row[item.value]==='1'" size="small" effect="dark" type="">{{item.list[0]}}</el-tag>
              <el-tag v-if="scope.row[item.value]==='2'" size="small" effect="dark" type="warning">{{item.list[1]}}
              </el-tag>
              <el-tag v-if="scope.row[item.value]==='3'" size="small" effect="dark" type="success">{{item.list[2]}}
              </el-tag>
            </div>
            <div v-else-if="item.identification==2">
              {{scope.row.mbs_pay_mode==3?scope.row[item.value]||'-':'-'}}
            </div>
            <div v-else-if="item.identification==3">
              {{scope.row[item.value]?scope.row[item.value]:'未交付'}}
            </div>
            <div v-else-if="item.identification==4">
              {{scope.row.mbs_sale_mode=='3'?scope.row.bmc_name:scope.row.write_off_person}}
            </div>
            <div v-else-if="item.identification==5">
              {{(scope.row[item.value]*1).toFixed(2)||'-'}}
            </div>
            <div v-else>
              {{scope.row[item.value]||'-'}}
            </div>
          </template>
        </el-table-column>

      </el-table>
      <!--  页面表格 end   -->
      <!--page start-->
      <el-pagination style="margin-top: 20px" background @size-change="handleSizeChange"
        @current-change="handleCurrentChange" :current-page="queryParam.currentPage" :page-sizes="[10, 20, 50, 100,200]"
        :page-size="queryParam.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="queryParam.totalRow">
      </el-pagination>
      <!--page end-->
    </el-card>
    <!--页面 end-->
  </div>
</template>

<script>
  export default {
    name: "TransactionRecord",
    data() {
      return {
        selectable: {
          value: '',
          list: [
            { value: '1', label: '按购买时间查询' },
            { value: '2', label: '按核销码查询' },
            { value: '3', label: '按特惠价手机号查询' }
          ],
        },
        queryMsg: {
          phone: '',
          code: '',
          time: '',
        },
        value1: '',
        phone1: '',
        pickerOptions: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        formData: {
          tableData: [
            { key: '订单号', value: 'mbs_out_trade_no' },
            { key: '设备编号', value: 'mbs_device_id' },
            { key: '核销码', value: 'write_off_code' },
            { key: '商品名称', value: 'mbgi_goods_name' },
            { key: '规格', value: 'mbgi_goods_describer' },
            { key: '特惠价手机号', value: 'mbs_guest_phone', identification: '2' },
            { key: '特惠价录入人员', value: 'cui_realname', identification: '2' },
            { key: '商户名称', value: 'mbmi_mer_name' },
            { key: '优惠类型', value: 'mbs_pay_mode', identification: '1', list: ['原价', '优惠价', '特惠价'] },
            { key: '支付金额（元）', value: 'mbs_money_1' , identification: '5'},
            { key: '支付时间', value: 'mbs_writetime' },
            { key: '交付方式', value: 'mbs_sale_mode', identification: '1', list: ['机器交付', '网点交付', '到店交付'] },
            { key: '交付时间', value: 'mbs_check_writetime', identification: '3' },
            { key: '核销人', value: 'write_off_person' , identification: '4'  },
            { key: '商家实收（元）', value: 'part4' },
            { key: '银行服务费（元）', value: 'part2' },
            { key: '渠道服务费（元）', value: 'part3' },
            { key: '平台服务费（元）', value: 'part1' },
          ]
        },
        tableData: [],
        /** 查询参数 */
        queryParam: {
          totalRow: 0,
          currentPage: 1,
          pageSize: this.customGlobal.pageSize,
        },
      }
    },
    watch: {
      'selectable.value'(newVue) {
        this.queryMsg.phone = ''
        this.queryMsg.code = ''
        this.queryMsg.time = ''
      }
    },
    created() {
      this.queryParam.currentPage = 1;
      this.query_name();
    },
    methods: {
      /**----分页方法-------start----*/
      handleSizeChange(val) {
        //  console.log(`每页 ${val} 条`);
        this.queryParam.pageSize = val;
        this.query_name();
      },
      handleCurrentChange(val) {
        //  console.log(`当前页: ${val}`);
        this.queryParam.currentPage = val;
        this.query_name();
      },
      //查询
      query_name() {
        if (this.selectable.value == '2' && this.queryMsg.code.length < 7) {
          this.$alert('核销码错误，请输入正确核销码', '提示信息', {
            confirmButtonText: '确定',
            callback: action => { }
          });
          return
        }
        let that = this;
        that.tableData = [];
        let _jsondata_ = {
          perPageSize: that.queryParam.pageSize,
          currentPage: that.queryParam.currentPage,
          obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
          obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
          obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
          obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
          obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
          mbs_guest_phone: this.queryMsg.phone,
          datatime: this.queryMsg.time[0],
          endtime: this.queryMsg.time[1],
          write_off_code: this.queryMsg.code
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        var a1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Transaction_record_select,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        a1.then((res) => {
          if (res.data.status === 'success') {
            loading.close();
            that.tableData = res.data.rows;
            that.tableData.forEach(res => {
              let code = '****' + res.mbs_out_trade_no.substring(res.mbs_out_trade_no.length - 3)
              let mbs_device_id = res.mbs_device_id.substring(res.mbs_device_id.length - 9)
              this.$set(res, "mbs_device_id", mbs_device_id);
              this.$set(res, "code", code);
            });
            that.queryParam.totalRow = res.data.total;
          } else {
            console.info("交易记录查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '交易记录查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '交易记录查询失败', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },
      //导出
      platformAddRole() {
        let that = this;
        let _jsondata_ = {
          perPageSize: that.queryParam.pageSize,
          currentPage: that.queryParam.currentPage,
          mbs_guest_phone: this.queryMsg.phone,
          datatime: this.queryMsg.time[0],
          endtime: this.queryMsg.time[1],
          write_off_code: this.queryMsg.code,
          obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
          obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
          obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
          obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
          obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
          exportTable: 'excel',
          export_humankey: '订单号;设备编号;核销码;商品名称;规格;特惠价手机号;特惠价录入人员;商户名称;优惠类型;支付金额;支付时间;交付方式;交付时间;核销人;商家实收;银行服务费;渠道商服务费;平台服务费',
          export_key: 'mbs_out_trade_no;equipment_no;write_off_code;mbgi_goods_name;mbgi_goods_describer;mbs_guest_phone;cui_realname;mbmi_mer_name;purchase_method;mbs_money_1_new;mbs_writetime;delivery_method;mbs_check_writetime;write_off_person;part4;part2;part3;part1',
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        var a1 = this.customGlobal.postHttpClient2(
          this.customGlobal.serverUrl.Transaction_record_select,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        a1.then((res) => {
          //if (res.data.status == 'success') {
          loading.close();
          this.$message({
            type: 'success',
            showClose: true,
            duration: 3000,
            dangerouslyUseHTMLString: true,
            message: '<span style="color:black;">导出数据成功</span>'
          });
          //}
          // else {
          //   console.info("导出数据失败");
          //   this.$alert(res.data.msg, '', {
          //     confirmButtonText: '确定',
          //     callback: action => {
          //       console.info('data:' + JSON.stringify(res.data));
          //       loading.close();
          //       if (res.data.msg === '登录已超时，请重新登录！') {
          //         this.$router.push({path: '/login'});
          //       }
          //     }
          //   });
          // }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '导出数据出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '导出数据出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },
      /**  表格列排序设置   */
      sortColumn(column) {
        //  console.log("已经排序项：" + JSON.stringify(column));
        let orderFlag = '';
        if (column.order != null) {
          if (column.order === "descending") {
            orderFlag = 'desc';
          } else if (column.order === "ascending") {
            orderFlag = 'asc';
          }
          //  console.info("属性名称："+ column.prop+",排序方式："+column.order);

          this.sortOrderCol = column.prop + " " + orderFlag;
          //  console.info("用户选择排序列："+this.sortOrderCol);
          // this.query_name();
        } else {
          this.sortOrderCol = '';
        }
      },
    }
  }
</script>

<style scoped>
  .TransactionRecord /deep/ .el-range-separator {
    padding: 0 5px;
    line-height: 32px;
    width: auto;
    color: #303133;
  }


  .account-plat-formitem /deep/ .el-form-item {
    margin-bottom: 3px !important;
  }

  .account-plat-card1 /deep/ .el-card__header {
    /*background:#f5f5f5 !important;*/
    background: #f2f3f8 !important;
    /*height: 10px!important;*/
    padding: 10px !important;
  }

  .account-plat-card2 /deep/ .el-card__body {
    padding-top: 5px !important;
    padding-bottom: 5px !important;
    padding-left: 20px !important;
    padding-right: 20px !important;
  }

  .account-plat-fixed-right /deep/ .el-table__fixed-right {
    height: 100% !important;
  }

  .account-plat-fixed /deep/ .el-table__fixed {
    height: 100% !important;
  }

  .box:after {
    content: ".";
    clear: both;
    height: 0;
    overflow: hidden;
    display: block;
    visibility: hidden;
  }
</style>


